.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.header {
  text-align: center;
  font-family: arial;
  color: white;
}

body {
  background-color: #2ecc71; 
  font-family: arial;
  overflow: hidden;
} 

.suggestion {
  margin-top: 2em; /* Adjust the top margin to move the section lower */
  margin-bottom: 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.5em; /* Adjust the desired spacing between elements */
}

.suggestion input[type="text"] {
  margin-right: -1.1em; /* Add margin only on the right side of the text field */
}

.suggestion button {
  margin-left: 0.5em; /* Add margin only on the left side of the "Insert" button */
}

.input-field {
  margin-right: 0.5em;
  width: 40%;
  height: 20px;
}

.insert-button {
  margin-left: 0.5em;
}

.generate-button {
  margin-left: 0.5em;
  background: #1ca9c9;
}


button {
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  color: var(--clr-neon);
  border: currentColor 0.125em solid;
  padding: 0.25em 1em;
  border-radius: 0.25em;
  background: #0fdb53;
}

.my-modal {
  width: '400px';
  margin: 'auto';
}

